export default {
	data() {
		return {
			selectType: 0,
			selectTypeArr: [{
				name: '全部',
				type: 0
			}, {
				name: '服饰',
				type: 1
			}, {
				name: '鞋靴',
				type: 2
			}, {
				name: '露营',
				type: 3
			}, {
				name: '运动',
				type: 4
			}, {
				name: '旅行',
				type: 5
			}, {
				name: '装备',
				type: 6
			}],
			
			curFilter: '', //当前筛选
			//推荐排序
			recomFilter: [{
				name: '综合排序',
				value: 1,
			}, {
				name: '价格从高到底',
				value: 2,
			}, {
				name: '价格从低到高',
				value: 3
			}, {
				name: '销量由高到底',
				value: 4
			}, {
				name: '评分由高到底',
				value: 5
			}],
			recomFilterVal: 1, //推荐排序默认值
		}
	},
	computed: {
		filterH() { //筛选列高度
			let sys = uni.getSystemInfoSync();
			return (sys.windowWidth / 750) * 540 + 'px'
		}
	},
	methods: {
		toCutType(idx) {
			this.selectTypeArr.map((v, i) => {
				if(idx == i) {
					this.selectType = v.type
				}
			})
		},
		selectFilter(type) {
			if(this.curFilter == type) {
				this.curFilter = ''
			}else {
				this.curFilter = type
			}
		},
		toCloseFilterbox() {
			this.curFilter = ''
		},
		toJumpProDetail(idx) {
			uni.navigateTo({
				url: '/pages/outdoorsPro/outProDetail'
			})
		}
	}
}
